import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons'
import './App.scss';
import config from './Config';
import { Link } from 'react-scroll';

const { name, description, contact, projects } = config

function App() {
  return (
    <>
      <header>
        <nav>
          <ul className="nav-list top">
            <li><Link href="#work" to="work" duration={750} smooth>Work</Link></li>
            <li><Link href="#contact" to="contact" duration={750} smooth>Contact</Link></li>
          </ul>
        </nav>
        <div className="center">
          <h1>{name}</h1>
          <p>{description}</p>
          <a className="contact-btn" href={`mailto:${contact.email}`}>Contact me</a>
        </div>
      </header>
      <main>
        <section id="work">
          <div className="section-container">
            <div>
              <h2>My work</h2>
              <ul className="work-list">
                {projects.map(project =>
                  <li className="work" key={project.name}>
                    <div className="work-description">
                      <h3>{project.name}</h3>
                      <p>{project.description}</p>
                      <ul className="work-languages">{project.tech.map(tech =>
                        <li key={tech}>{tech}</li>
                      )}
                      </ul>
                      <div className="work-links">
                        {project.website &&
                          <a href={project.website} target="_blank" rel="noreferrer">View website</a>}
                        {project.website == null && project.repo &&
                          <a href={project.repo} target="_blank" rel="noreferrer">View repo</a>}
                        {project.repo &&
                          <a href={project.repo} target="_blank" rel="noreferrer" className="icon"><FontAwesomeIcon icon={faGithub} size={"lg"} /></a>}
                      </div>
                    </div>
                    <div className="work-image">
                      {project.image && (project.website || project.repo) && <a href={project.website ? project.website : project.repo} target="_blank" rel="noreferrer"><img src={project.image} alt={project.name} /></a>}
                      {project.image && !(project.website || project.repo) && <img src={project.image} alt={project.name} />}
                    </div>
                  </li>
                )}
              </ul>
            </div>
          </div>
          <div className="container">
            <div id="about">
            </div>
          </div>
        </section>
        <section id="contact">
          <div className="section-container">
            <div className="contact">
              <h2>Contact me</h2>
              <p className="contact-description">{contact.description}</p>
              <a className="contact-btn" href={`mailto:${contact.email}`}>{contact.email}</a>
            </div>
          </div>
        </section>
      </main>
      <footer>
        <ul className="nav-list">
          <li><a href="https://github.com/josetr" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faGithub} size={"3x"} /></a></li>
          <li><a href="https://www.linkedin.com/in/josetrdev" target="_blank" rel="noreferrer"><FontAwesomeIcon icon={faLinkedin} size={"3x"} /></a></li>
        </ul>
        <p>Â© 2021 {name}</p>
      </footer>
    </>
  );
}

export default App;
